@import url(https://fonts.googleapis.com/css2?family=Archivo&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Archivo:wght@700&display=swap);
body{
    margin:0;
    width:100%;
    height:100vh;
    -ms-scroll-chaining:none;
        overscroll-behavior:none;
    font-family:"Archivo", "san-serif";
}

#card-wrapper{
    margin:1em 0 4em 0;
}
.card{
    margin:.5em 1em !important;
}
.chart{
    width:75%;
    margin:2em 0 3em 0;
}
#camulative-map-wrapper{
    width:600px;
    display:flex;
    justify-content:center;
    align-items:center;
}
.leaflet-container{
    width:100%;
    height:400px;
}
.leaflet-marker-icon{
    opacity:0.6;
}
#active-map-wrapper{
    width:600px;
    display:flex;
    justify-content:center;
    align-items:center;
}
.leaflet-container{
    width:100%;
    height:400px;
}
.leaflet-marker-icon{
    opacity:0.6;
}
#app-wrapper{
    display:flex;
    flex-direction:column;
    align-items:center;
}
#header{
    font-size:3em;
    display:flex;
    font-family:"Archivo";
}
#icon{
    position:relative;
    top:65px;
}
#maps-wrapper{
    display:flex;
    flex-direction:column;
    align-items:center;
}
#map-toggler{
    display:flex;
    align-self:flex-start;
    margin-left:1em;
}
.toggler{
    border:1px solid black;
    margin-right:1em;
}
